	at width 445px the videos no long shrink and they get fucky



*/



body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.notSelectable{
	user-select: none;
	-moz-user-select: none;
	-webkit-user-drag: none;
	-webkit-user-select: none;
	-ms-user-select: none;
}

.header{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding-right: 75px;
	padding-left: 75px;
	text-align: center;
}

.title{
	color: #D23943;
	font-size: 5em;
	margin: 50px;
}

hr{
	border: 0;
	height: 1px;
	background-color: black;
	width: 100%;
	/* background-image: linear-gradient(to right, #ccc, #333, #ccc); */
}

.subtitle{
	color: #D23943;
	font-size: 3em;
	width: 100%;
	text-align: center;
	margin-top: 125px;
}

.live{
	background-color: #D23943;
	color: white;
	display: inline-flex;
	align-items: center;
	padding-bottom: 2px;
	cursor: pointer;
	transition: transform 300ms;
}

.live:hover{
	transform: scale(1.05);
}

.live:hover > .circle{
	animation: blink 1s steps(1, start) infinite;
}

.circle{
	height: 30px;
	width: 30px;
	background-color: white;
	border-radius: 50%;
	display: inline-block;
	margin-bottom: -1px;
}

.info{
	width: 75%;
}

.note{
	font-weight: bold;
}

.videoGrid{
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
	grid-gap: 50px;
	width: 85%;
	margin-left: auto;
	margin-right: auto;
	

	/* display: grid;
	grid-template-columns: repeat(2,  minmax(250px, 1fr));
	gap: 10px; */
}

.videoWrapper{
	position: relative;
	padding-bottom: 56.25%;
}

.video{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	/* border-radius: 50px; */
	/* TODO fix the weird height on the iframes (it has smth to do with setting the heihgt="" and width="" on the Video element */
}

.btn{
	display: block;
	width: 150px;
	margin-left: auto;
	margin-right: auto;
	margin-top: 50px;
	margin-bottom: 50px;
	cursor: pointer;
	border-radius: 5px;
	border: 1px solid black;
	background-color: white;
	padding: 5px;
	transition: opacity 200ms;
	font-size: 1.2em;
}

.btn:hover{
	opacity: 0.5;
}

.transparentText{
	text-align: center;
	width: 100%;
	margin-top: 50px;
	margin-bottom: 50px;
	opacity: 0.75;
}

.footer{
	width: 100%;
	text-align: center;
	margin-top: 100px;
}

@keyframes blink{
	0%{ opacity: 1}
	50%{ opacity: 0; }
}